import React, { Component } from 'react';
import {Link} from 'react-router-dom';

class PlayerInput extends Component {
	constructor(props) {
		super(props);
		this.onChangeHandler = this.onChangeHandler.bind(this);
		this.onClickHandler = this.onClickHandler.bind(this);
		this.lastPlayer = localStorage.getItem('lastPlayer') ? JSON.parse(localStorage.getItem('lastPlayer')) : {name: '', points: ''};
		this.state = {
			playerName: this.lastPlayer.name
		}
	}
  onChangeHandler(e) {
    this.setState({playerName: e.target.value})
  }
	onClickHandler() {
		this.lastPlayer = {name: this.state.playerName, points: this.props.points};
		let lpString = JSON.stringify(this.lastPlayer);
		localStorage.setItem('lastPlayer', lpString);
		
		let n = localStorage.length - 1; //    lastPlayer
		if(n === 0)
			localStorage.setItem(0, lpString); //      
		else {
			let i = 0;
			for(; i < n; i++) {
				let storagePlayer = JSON.parse(localStorage.getItem(i));
				if(this.lastPlayer.points >= storagePlayer.points){
					for(let j = 0; j < n - i; j++) {
						localStorage.setItem((n - j), localStorage.getItem(n - j - 1)); //  
					}
					localStorage.setItem(i, lpString); //      
					break;
				}
			}
			if(i === n && i <= 15) // 15   
				localStorage.setItem(i, lpString); //      
		}
		
	}
	render() {
		let nameInput = this.props.isHidden? <div></div> : 
			<div>
				<input
					className='player-input'
					value={this.state.playerName}
					onChange={this.onChangeHandler}
					placeholder='  '
				/>
				<Link to='/EndPage' onClick={this.onClickHandler}>
					<div className='restart-btn'></div>
				</Link>
			</div>

		return nameInput;
	}
}

export default PlayerInput;


